import type {
  InitRookProps,
  UpdateUserIdProps,
  UserIdResult,
} from '../definitions';
import { RookSDK } from '../plugin';

export const RookConfig = {
  initRook: (credentials: InitRookProps): Promise<boolean> => {
    return RookSDK.initRook({
      ...credentials,
      enableLogs: credentials.enableLogs || false,
    });
  },

  updateUserId: (userId: UpdateUserIdProps): Promise<boolean> => {
    return RookSDK.updateUserId(userId);
  },

  getUserId: (): Promise<UserIdResult> => {
    return RookSDK.getUserId();
  },

  clearUserId: (): Promise<boolean> => {
    return RookSDK.clearUserId();
  },

  syncUserTimeZone: (): Promise<boolean> => {
    return RookSDK.syncUserTimeZone();
  },

  enableAppleHealthSync: (): Promise<boolean> => {
    return RookSDK.enableAppleHealthSync();
  },

  disableAppleHealthSync: (): Promise<boolean> => {
    return RookSDK.disableAppleHealthSync();
  },

  isAppleHealthSyncEnable: (): Promise<boolean> => {
    return RookSDK.isAppleHealthSyncEnable();
  },
};
